import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { CustomerRoutingModule } from './customer-routing.module';
import { CustomerComponent } from './customer.component';
import { UserTopNavComponent } from '../layout/components/user-top-nav/user-top-nav.component';
import { UserSideBarComponent } from '../layout/components/user-side-bar/user-side-bar.component';
import { MatIconModule, MatMenuModule, MatToolbarModule, MatListModule, MatButtonModule, MatCardModule } from '@angular/material';
import { TranslateModule } from '@ngx-translate/core';
import { ViewMediaComponent } from './view-media/view-media.component';
import { VgCoreModule } from 'videogular2/core';
import { VgControlsModule } from 'videogular2/controls';
import { VgOverlayPlayModule } from 'videogular2/overlay-play';
import { VgBufferingModule } from 'videogular2/buffering';
import { VgImaAdsModule } from 'videogular2/ima-ads';

@NgModule({
  declarations: [CustomerComponent,UserTopNavComponent,UserSideBarComponent,ViewMediaComponent],
  imports: [
    CommonModule,
    CustomerRoutingModule,
    MatIconModule,
    MatCardModule,
    MatMenuModule,
    TranslateModule,
    MatToolbarModule,
    MatListModule,
    MatButtonModule,
    VgCoreModule,
    VgControlsModule,
    VgOverlayPlayModule,
    VgBufferingModule,
    VgImaAdsModule
  ],
  entryComponents:[ViewMediaComponent]
})
export class CustomerModule { }
